body {
	font-size: 15px;
	line-height: 1.4;
	overflow-x:	hidden; }

@include font-face('Exo2-Medium', 'Exo2-medium', '../fonts/Exo2/Exo2-Medium');
@include font-face('HelveticaNeueCyr-Light', 'HelveticaNeueCyr-Light', '../fonts/HelveticaNeue/HelveticaNeueCyr-Light');

// @media(max-width: 1199)
// @media(max-width: 991)
// @media(max-width: 767)
// @media(max-width: 575)

.hero-title {
	font-family: 'Exo2-medium';
	text-align: center;
	font-size: 36px;
	margin-bottom: 15px;
	margin-top: 20px; }

.tabs {
	display: flex;
	justify-content: center;
	align-items: center;
	margin-bottom: 18px;

	@media(max-width: 767px) {
		flex-wrap: wrap; } }



.tab {
	margin-right: 10px;
	cursor: pointer;
	transition: transform .3s ease;
	&:last-child {
		margin-right: 0; }
	&:hover {
 }		//

	@media(max-width: 767px) {
		width: 100%;
		margin-bottom: 10px;
		margin-right: 0;
		display: flex;
		justify-content: center; } }


.logo-prod {
	background-color: #f5f3e8;
	height: 48px;
	width: 140px;
	display: flex;
	justify-content: center;
	align-items: center;


	&_color-1 {
		background-color: #8fb52c; }

	&_all {
		width: 150px;
		p {
			font-family: 'HelveticaNeueCyr-Light';
			font-size: 18px;
			border-bottom: 1px dotted; } }

	&_small {
		width: 72px;
		height: auto;
		padding: 4px 14px 6px 14px;
		img {
			width: 100%; } } }

.products {
	max-width: 940px;
	margin: 0 auto;
	margin-bottom: 30px; }

.products__inner {
	display: flex;
	flex-wrap: wrap;
	margin-left: -15px;
	margin-right: -15px;

	@media(max-width: 991px) {
		justify-content: center; }

	@media(max-width: 575px) {
		margin: 0; } }


.product {
	width: 300px;
	margin-right: 10px;
	margin-left: 10px;
	margin-bottom: 40px;

	@media(max-width: 575px) {
		width: 100%;
		margin-right: 0;
		margin-left: 0; }

	&__img {
		display: block;
		width: 100%; }

	&__sub-title {
		font-size: 15px;
		color: #3c2f20;
		font-family: 'HelveticaNeueCyr-Light';
		margin-top: 10px; }

	&__title {
		font-size: 30px;
		color: #3c2f20;
		font-family: 'Exo2-medium';
		line-height: 1;
		display: flex;
		flex-direction: column;
		align-items: flex-start;
		margin-bottom: 15px;
		transition: all .3s ease;
		cursor: pointer;
		&-major {
			margin-bottom: 5px; }
		&-sub {
			font-size: 20px; }
		span {
			display: inline-block;
			border-bottom: 1px dotted; }

		&:hover {
			color: #5b84f0; } }

	&__body {
		margin-bottom: 5px; }

	&__text {
		font-size: 15px;
		font-family: 'HelveticaNeueCyr-Light';
		color: #1d1813; } }
