body {
  font-size: 15px;
  line-height: 1.4;
  overflow-x: hidden;
}

@font-face {
  font-family: "Exo2-Medium";
  font-weight: normal;
  font-style: normal;
  src: url("../fonts/Exo2/Exo2-Medium.eot");
  src: local("Exo2-Medium Exo2-medium"), local("Exo2-Medium-Exo2-medium"), url("../fonts/Exo2/Exo2-Medium.eot?#iefix") format("embedded-opentype"), url("../fonts/Exo2/Exo2-Medium.woff") format("woff2"), url("../fonts/Exo2/Exo2-Medium.woff") format("woff"), url("../fonts/Exo2/Exo2-Medium.ttf") format("truetype");
}

@font-face {
  font-family: "HelveticaNeueCyr-Light";
  font-weight: normal;
  font-style: normal;
  src: url("../fonts/HelveticaNeue/HelveticaNeueCyr-Light.eot");
  src: local("HelveticaNeueCyr-Light HelveticaNeueCyr-Light"), local("HelveticaNeueCyr-Light-HelveticaNeueCyr-Light"), url("../fonts/HelveticaNeue/HelveticaNeueCyr-Light.eot?#iefix") format("embedded-opentype"), url("../fonts/HelveticaNeue/HelveticaNeueCyr-Light.woff") format("woff2"), url("../fonts/HelveticaNeue/HelveticaNeueCyr-Light.woff") format("woff"), url("../fonts/HelveticaNeue/HelveticaNeueCyr-Light.ttf") format("truetype");
}

.hero-title {
  font-family: 'Exo2-medium';
  text-align: center;
  font-size: 36px;
  margin-bottom: 15px;
  margin-top: 20px;
}

.tabs {
  display: flex;
  justify-content: center;
  align-items: center;
  margin-bottom: 18px;
}

@media (max-width: 767px) {
  .tabs {
    flex-wrap: wrap;
  }
}

.tab {
  margin-right: 10px;
  cursor: pointer;
  transition: transform .3s ease;
}

.tab:last-child {
  margin-right: 0;
}

@media (max-width: 767px) {
  .tab {
    width: 100%;
    margin-bottom: 10px;
    margin-right: 0;
    display: flex;
    justify-content: center;
  }
}

.logo-prod {
  background-color: #f5f3e8;
  height: 48px;
  width: 140px;
  display: flex;
  justify-content: center;
  align-items: center;
}

.logo-prod_color-1 {
  background-color: #8fb52c;
}

.logo-prod_all {
  width: 150px;
}

.logo-prod_all p {
  font-family: 'HelveticaNeueCyr-Light';
  font-size: 18px;
  border-bottom: 1px dotted;
}

.logo-prod_small {
  width: 72px;
  height: auto;
  padding: 4px 14px 6px 14px;
}

.logo-prod_small img {
  width: 100%;
}

.products {
  max-width: 940px;
  margin: 0 auto;
  margin-bottom: 30px;
}

.products__inner {
  display: flex;
  flex-wrap: wrap;
  margin-left: -15px;
  margin-right: -15px;
}

@media (max-width: 991px) {
  .products__inner {
    justify-content: center;
  }
}

@media (max-width: 575px) {
  .products__inner {
    margin: 0;
  }
}

.product {
  width: 300px;
  margin-right: 10px;
  margin-left: 10px;
  margin-bottom: 40px;
}

@media (max-width: 575px) {
  .product {
    width: 100%;
    margin-right: 0;
    margin-left: 0;
  }
}

.product__img {
  display: block;
  width: 100%;
}

.product__sub-title {
  font-size: 15px;
  color: #3c2f20;
  font-family: 'HelveticaNeueCyr-Light';
  margin-top: 10px;
}

.product__title {
  font-size: 30px;
  color: #3c2f20;
  font-family: 'Exo2-medium';
  line-height: 1;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  margin-bottom: 15px;
  transition: all .3s ease;
  cursor: pointer;
}

.product__title-major {
  margin-bottom: 5px;
}

.product__title-sub {
  font-size: 20px;
}

.product__title span {
  display: inline-block;
  border-bottom: 1px dotted;
}

.product__title:hover {
  color: #5b84f0;
}

.product__body {
  margin-bottom: 5px;
}

.product__text {
  font-size: 15px;
  font-family: 'HelveticaNeueCyr-Light';
  color: #1d1813;
}
